import Web3 from 'web3';
const BN = arg => Web3.utils.toBN(arg);
const PROP = [0.3, 0.2, 0.1, 0.05, 0.05, 0.05, 0.05, 0.05, 0.05, 0.05, 0.05, 0.05, 0.05, 0.1, 0.2];

export default class Income {
    constructor({ Main, Relations }) {
        this.Main = Main;
        this.Relations = Relations;
    }
    
    get(address, propSub = 0) {
        if (propSub >= 15) return BN(0);
        
        return new Promise((resolve, reject) => {
            this.Relations.recommendList(address).call((err, childs) => {
                if (err) reject(err);
                if (childs.length <= 0) resolve(BN(0));
                
                Promise.all(
                    childs.map(child => this.Main.addressInvestMapping(child).call())
                ).then(async incomes => {
                    let income = BN(0);
                    
                    incomes.forEach(info => {
                        if (info.totalAmount > 0) {
                            income = income.add(
                                BN(info.totalAmount)
                                .div(BN(100))
                                .mul(BN(PROP[propSub] * 100))
                            );
                        }
                    })
                    
                    for (let key in childs) {
                        income = income.add(await this.get(childs[key], propSub + 1))
                    }
                    
                    resolve(income)
                })
            })
        })
    }
}